.PosPlusTick {
  margin-top: 11px;
  margin-bottom: 0px;
  padding-right: 15px;
  margin-right: 0px;
}
.Pos {
  margin-left: 11px;
  margin-right: 0px;
  width: 100%;
  padding-right: 0px;
}
.Pos_text {
  width: 100%;
  margin-right: 20px;
}

.hidden {
  display: none;
}

@media (max-width: 1200px) {
  .Pos_text  {
    min-width: 100%;
  }
}

@media (max-width: 400px) {
  .PosPlusTick {
    padding-right: 0px;
  }
}
